.home{
    background-image: url("../media//bg.jpg");
    background-repeat: no-repeat,repeat;
    background-size: cover;
    height: 100vh;
    
}
.headingT{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12rem 1rem;
    padding-bottom: 1rem;
}

.lmbtn{
    padding: 0.5rem 1.5rem;
    font-size: larger;
    border-radius: 0.5rem;
    background-color: rgb(0,0,0);
    color:#fffffc;
}

.aboutus{
    background-color: rgb(252,251,247);
    padding: 1.5rem 0;
}
.line{
    background-image: linear-gradient(54deg, #3335c4 0, #9cfeff 100%);
    height: 6px;
    width: 45%;
    border-radius: 1rem;
    margin: auto;
    margin-bottom: 1rem;
}
.hd1{
    font-size:3.5rem;
    font-weight: 500;

}
.hd2{
    font-family: Poppins, sans-serif;
    font-size: x-large;
    margin-bottom: 2rem;
   
}
.para1{
    color: rgb(127,133,138);
}
.imgDiv{
    background-color: rgb(226,242,255);
    padding: 2rem;
    border-radius: 1rem;
}
.lmbtn2{
    padding: 0.5rem 1rem;
    font-size: larger;
    border-radius: 0.5rem;
    background-color: transparent;
    color:#111110;
    margin-top: 2rem;
    border: 1px solid black;
}
.latestimg{
    height: 200px;
    width: 200px;
    object-fit: cover;
    border: 8px solid white;
    border-radius: 0.7rem;
    margin-bottom: 2rem;
}
.services{
    background-image: linear-gradient(54deg, rgb(210,242,231) 0, rgb(224,242,247) 100%);
}
.gridDiv{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    padding: 1rem;
    gap: 1rem;
}
.griditem{
    background-color: rgb(255,255,255);
    padding: 1rem;
    border-radius: 1rem;
    
   
}
.gridTopDiv{
    display: flex;
    flex-direction: row;
}

.para3{
    width: 80%;
    color: #8a8a8a;
    margin: 25px auto 0;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 32.4px;
}
.span1:first-child {
    margin-left: 40px;
}
.span1 {
    flex-shrink: 0;
    color: #707070;
    font-size: 88px;
    font-style: normal;
    font-weight: 600;
    line-height: 123.2px;
    top: 35px;
    left: 40px;
    opacity: .2;
    transition: .3s;
}
.gridtitle{
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-left: -20px;
    margin-top: 35px;
    transition: .3s;
}
.whychoose{
    background-image:linear-gradient(54deg, rgb(210,242,231)10%, rgb(251,251,247) 100%) ;
    color: white;
}
.meetai{
    background-color:rgb(39,46,92) ;
    height: 70vh;
    width: 100%;
}
.truai{
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-size: 200%;
    animation: TruAi_background-pan__wS4NA 2s linear infinite;
    background-image: linear-gradient(to right, var(--tw-gradient-stops));
    --tw-gradient-from: #9747ff;
    --tw-gradient-from-position: 0%;
    --tw-gradient-to: #9747ff;
    --tw-gradient-stops: var(--tw-gradient-from) var(--tw-gradient-from-position), var(--tw-gradient-to) var(--tw-gradient-to-position);
    --tw-gradient-via: #2f80ed;
    --tw-gradient-via-position: 50%;
    --tw-gradient-stops: var(--tw-gradient-from) var(--tw-gradient-from-position), var(--tw-gradient-via) var(--tw-gradient-via-position), var(--tw-gradient-to) var(--tw-gradient-to-position);
    --tw-gradient-to: #ee216b;
    --tw-gradient-to-position: 100%;
}